import Module from "module";
import path from "path";

/**
 * We need to use private Module methods in this polyfill
 */
interface IModulePrivateMethods {
  _nodeModulePaths: (directory: string) => Array<string>;
  _compile: (src: string, file: string) => void;
}

const fallback = (filename: string): NodeRequire => {
  const mod = new Module(filename) as Module & IModulePrivateMethods;

  mod.filename = filename;
  mod.paths = (
    Module as typeof Module & IModulePrivateMethods
  )._nodeModulePaths(path.dirname(filename));
  mod._compile(`module.exports = require;`, filename);

  return mod.exports;
};

// Polyfill Node's `Module.createRequireFromPath` if not present (added in Node v10.12.0).
// XXX(ndhoule): Module.createRequireFromPath is deprecated and was removed in Node v16
export const createRequireFromPath =
  Module.createRequire ||
  // @ts-expect-error TS(2339)
  Module.createRequireFromPath ||
  fallback;

export const requireFromInstalledGatsbyDist = (modulePath: string) => {
  try {
    const requireFromSite = createRequireFromPath(
      path.join(process.cwd() + `/:internal:`),
    );
    return requireFromSite(path.join(`gatsby/dist/`, modulePath));
  } catch (e) {
    if (!e.message.includes(`Cannot find module`)) {
      throw e;
    }

    return null;
  }
};
export const resolveFromContentEngine = (
  modulePath: string,
  rootDir = process.cwd(),
) => {
  try {
    const requireFromSite = createRequireFromPath(
      path.join(rootDir + `/:internal:`),
    );
    return requireFromSite.resolve(
      path.join(`@netlify/content-engine/dist/`, modulePath),
    );
  } catch (e) {
    if (!e.message.includes(`Cannot find module`)) {
      throw e;
    }

    return null;
  }
};
